/* hamburger */
label .menu {
  position: absolute;
  top: -100px;
  right: 0;
  z-index: 100;
  width: 200px;
  height: 200px;
  background: #FFF;
  border-radius: 50% 50% 50% 50%;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #FFF;
  cursor: pointer;
}

label .hamburger {
  position: absolute;
  top: 135px;
  left: 120px;
  width: 30px;
  height: 2px;
  background: #C6C6C6;
  display: block;
  -webkit-transform-origin: center;
  transform-origin: center;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}

label .hamburger:after {
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #C6C6C6;
}

label .hamburger:before {
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #C6C6C6;
  top: -10px;
}

label .hamburger:after {
  bottom: -10px;
}

label input {
  display: none;
}

label input:checked + .menu {
  box-shadow: 0 0 0 100vw #FFF, 0 0 0 100vh #FFF;
  border-radius: 0;
}

label input:checked + .menu .hamburger {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

label input:checked + .menu .hamburger:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  bottom: 0;
}

label input:checked + .menu .hamburger:before {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  top: 0;
}

label input:checked + .menu + ul {
  opacity: 1;
}

label > ul {
  list-style-type: none;
  z-index: 200;
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  -webkit-transition: .25s 0s ease-in-out;
  transition: .25s 0s ease-in-out;
}

ul, ol {
  margin: 0 !important;
}

label ul li {
  margin-bottom: .2em;
  padding-top: 10px;
  font-size: 2em;
  text-align: center;
  display: block;
  text-transform: uppercase;
  color: #a6a2a2;
  text-decoration: none;
}

label ul li a {
  color: #a6a2a2;
  text-decoration: underline;
}

label ul li a:visited {
  color: #a6a2a2;
  text-decoration: underline;
}

label ul li a:hover {
  text-decoration: none;
  color: #a6a2a2;
}

label ol {
  padding: 0;
}

label ol li {
  margin-bottom: 0em;
  padding: 0;
}

label ol li a {
  margin-bottom: 0em;
  text-decoration: none;
  font-size: 0.8em;
  color: #a6a2a2;
}

label ol li a:visited {
  margin-bottom: 0em;
  text-decoration: none;
  font-size: 0.8em;
  color: #a6a2a2;
}

label ol li a:hover {
  color: #a6a2a2;
  transform: scale(1.1);
  text-decoration: none;
}

